    //
    //     this.props.history.push(`/accesspoll1?adress=${adress}&id=${id}&vote=${script}`);
    //     // this.props.history.push(`/getresult?adress=${this.state.f}&id=${this.state.s}&vote=${script}`);
    //
    //   })
    //   .catch(e => alert(e));

    nos.getStorage(scriptHash, this.state.adress + this.state.id).then(response => {
      nos.invoke({
        scriptHash,
        operation: "access",
        args: [
          response
        ]
      })
        .then((script) => {

          this.props.history.push(`/accesspoll1?adress=${adress}&id=${id}&vote=${script}`);
          // this.props.history.push(`/getresult?adress=${this.state.f}&id=${this.state.s}&vote=${script}`);

        })
        .catch(e => alert(e));
    }).catch(error => alert(error));

    // nos.invoke({
    //   scriptHash,
