import React, { Component } from "react";
import PropTypes from "prop-types";
//import { injectNOS, nosProps } from "@nosplatform/api-functions/lib/react";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";

const nos = window.NOS.V1;
const scriptHash = "7b94582be74ac15a8b0d53d81187eb62c89c7bcf";


const getParameterByName = (name, url) => {
  if (!url) url = window.location.href;
  name = name.replace(/[\[\]]/g, "\\$&");
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
};

class GetResult extends Component {
  constructor(props) {
    super(props);
  }

  yourFunction() {

    const id = getParameterByName("id", window.href);
    const vote = getParameterByName("vote", window.href);
    const adress = getParameterByName("adress", window.href);
    // return value of results
    nos.invoke({
      scriptHash,
      operation: "result",
      args: [

        this.state.adress,
        this.state.id
      ]
    })
      .then((script) => {
        this.props.history.push(`/getresult1?result=${script}`);

      })
      .catch(e => alert(e));
  }


  render() {
    return (
      <div>


        <div>
          <div style={{ margin: 50 }}>Poll owner add:</div>

          <input placeholder="Poll owner add:"
                 onChange={(e) => this.setState({ adress: e.target.value })}
                 style={{
                   height: 20,
                   backgroundColor: "#ffffff",
                   paddingLeft: 15,
                   paddingRight: 15
                 }}></input>

          <div style={{ margin: 30 }}>Poll Id::</div>
          <input placeholder="Poll id"
                 onChange={(e) => this.setState({ id: e.target.value })}
                 style={{
                   height: 20,
                   backgroundColor: "#ffffff",
                   paddingLeft: 15,
                   paddingRight: 15
                 }}></input>


          <div style={{ display: "flex", flexDirection: "row", justifyContent: "center" }}>
            <button style={{
              backgroundColor: "green",
              width: 100,
              height: 50,
              padding: 15,
              marginBottom: 60
            }}
                    onClick={() => {
                      this.yourFunction();
                    }}>
              Get Access
            </button>

            <Link to="/" style={{
              backgroundColor: "red",
              width: 100,
              height: 50,
              padding: 15,
              marginBottom: 60
            }}>Cancel</Link>
          </div>
      </div></div>
    );
  }
}

export default withRouter(GetResult);
