import React,{Component} from 'react'
//import {Text,TextInput,View,TouchableOpacity} from 'react-native'
//import RadioForm, {RadioButton, RadioButtonInput, RadioButtonLabel} from 'react-native-simple-radio-button';
import PropTypes from "prop-types";
//import { injectNOS, nosProps } from "@nosplatform/api-functions/lib/react";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";

const nos = window.NOS.V1;
const scriptHash = '7b94582be74ac15a8b0d53d81187eb62c89c7bcf';


const getParameterByName = (name, url) => {
  if (!url) url = window.location.href;
  name = name.replace(/[\[\]]/g, "\\$&");
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
};


class AccessPoll1 extends Component{
    constructor(props){
        super(props)
        this.state = {
            size: ''
          };
    }

    yourFunction(){

      const id = getParameterByName("id", window.href);
      const vote = getParameterByName("vote", window.href);
      const adress = getParameterByName("adress", window.href);
// access was return this is string "c42395772fb745cd556596af5d83f7b164292c74ee4afc706e8be287af021296"
//      this is not question put that string at the moment
        nos.invoke( {scriptHash , operation:"vote" , args:[adress, id, this.state.size] })
          .then(script=>{
            nos.invoke( {scriptHash , operation:"result" , args:[adress, id] })
              .then(result=> {
                alert(`Result: ${result}`)
                this.props.history.push(`/accesspoll2/?result=${result}`)
              }
              )
              .catch(error=>alert(`Error ${error}`))

            // this.props.history.push(`/accesspoll2/${script}`)
          })
          .catch(e=>alert(e));

    }


    render(){
      const vote = getParameterByName("vote", window.href);

      return(
            <div>

            <div style={{margin:30}}>
              {`Question: ${vote}`}
            {/*question from previous screen*/}
            </div>
      
      <ul>
        <li>
          <label>
            <input
              type="radio"
              value="yes"
              checked={this.state.size === "A"}
              onChange={()=>this.setState({size:"A"})}
            />
            yes
          </label>
        </li>
        
        <li>
          <label>
            <input
              type="radio"
              value="no"
              checked={this.state.size === "B"}
              onChange={()=>this.setState({size:"B"})}
            />
            no
          </label>
        </li>
      </ul>


        <div style={{display:'flex',flexDirection:'row',justifyContent:'center'}}>
          <button style={{backgroundColor:'green',width:100,height:50,padding:15,marginBottom:60}}
                  onClick={() => {
                    this.yourFunction();}}>
                Submit
                </button>

          <button style={{
            backgroundColor: "red",
            width: 100,
            height: 50,
            padding: 15,
            marginBottom: 60
          }} onClick={() => this.props.history.push(`/`)}>Cancel</button>
            </div>
        </div>
        );
    }

}
export default withRouter(AccessPoll1);
